	.menu_trigger_container
	{
		padding-right: 15px;
	}
	.cat_menu li
	{
		height: 36px;
		padding-left: 15px;
		padding-right: 15px;
	}
	.cat_menu li a
	{
		font-size: 13px;
		line-height: 36px;
	}
	.cat_menu li ul
	{
		left: 50%;
		z-index: 10;
	}
	.page_menu_item > a
	{
		font-size: 12px;
	}
	.page_menu_selection li a
	{
		font-size: 12px;
	}
	.banner_product_image
	{
	    right: 15px;
	}
	.button
	{
		height: 32px;
	}
	.button a
	{
		font-size: 12px;
		line-height: 32px;
		padding-left: 25px;
		padding-right: 25px;
	}
	.rating_r i::before
	{
		font-size: 14px;
	}
	.shop_product_count, 
	.shop_sorting
	{
		float: none;
	}
	.shop_sorting
	{
		margin-top: 7px;
	}
	.shop_page_nav
	{
		height: 40px;
	}
	.page_prev, .page_next
	{
		width: 30px;
	}
	.page_nav li
	{
		width: 30px;
		height: 40px;
	}
	.single_product
	{
		padding-top: 80px;
	}
	.image_selected
	{
		height: calc(100vw - 30px);
	}
	.image_list li
	{
		height: calc((100vw - 30px) / 3 - 15px);
	}
	.product_name
	{
		font-size: 24px;
	}
	.product_quantity
	{
		float: none;
	}
	.product_color
	{
		float: none;
		margin-top: 15px;
	}
	.cart_button
	{
		font-size: 13px;
