	padding-left: 15px;
}
.order_total_title
{
	display: inline-block;
	font-size: 14px;
	color: rgba(0,0,0,0.5);
	line-height: 60px;
}
.order_total_amount
{
	display: inline-block;
	font-size: 18px;
	font-weight: 500;
	margin-left: 26px;
	line-height: 60px;
}
.cart_buttons
{
	margin-top: 60px;
	text-align: right;
}
.cart_button_clear
{
	display: inline-block;
	border: none;
	font-size: 16px;
	font-weight: 400;
	line-height: 48px;
	color: rgba(0,0,0,0.5);
	background: #FFFFFF;
	border: solid 1px #b2b2b2;
	padding-left: 35px;
	padding-right: 35px;
	outline: none;
	cursor: pointer;
	margin-right: 26px;
	vertical-align: top;
}
